<template>
  <div class="register">
    <div class="app-logo">

      <div class="logo">
        <van-image
          round
          width="2.133rem"
          height="2.133rem"
          :src="NameSrc"
        />
      </div>

    </div>
    <div>
      <van-cell-group>
        <van-field
          class="trimming"
          v-model="register.account.value"
          type="text"
          label="用户名"
          placeholder="请输入用户名"
          left-icon="manager"
          :error-message="register.account.testingc ? '' : register.account.tips"
          clickable
          clearable
          maxlength="16"
          @input="testingcAcount()"
          @blur="showUserimg()"
        />
        <van-field
          class="trimming"
          v-model="register.pwd.value"
          :type="register.pwd.see ? 'password' : 'text'"
          label="密码"
          placeholder="请输入密码"
          left-icon="lock"
          :right-icon="register.pwd.see ? 'closed-eye' : 'eye-o'"
          :error-message="register.pwd.testingc ? '' : register.pwd.tips"
          @click-right-icon="checkPassword()"
          clickable
          clearable
          maxlength="18"
          @input="testingcPwd()"
        />

      </van-cell-group>
      <div class="register-form">
        <van-button
          class="register-btn"
          :disabled="!refer"
          :loading="island"
          type="info"
          block
          loading-text="登陆中..."
          text="登陆"
          @click="landLoading()"
       />
      </div>
      <div class="login">没有账号？<router-link to="/Register">注册</router-link></div>
    </div>
  </div>
</template>

<script>

  export default {
    name: 'register',

    data() {
      return {
        // 输入框信息
        // 账号
        register: {
          account: {
            value: '',
            testingc: true,
            tips: '账号格式错误,允许字母数字下划线',
            pat: /^[a-zA-Z0-9_]{4,15}$/
          },
          // 密码
          pwd: {
            value: '',
            testingc: true,
            tips: '密码以字母开头,允许包含字母、数字下划线',
            see: true,
            pat: /^[a-zA-Z]\w{5,17}$/
          }
        },

        // 登陆加载
        island: false,

        // 是否显示用户图片
        IsUserimg: false,

        // 用户图片
        NameSrc: null,

      }
    },
    //计算属性
    computed: {
      // ...mapState(['pwd1', 'pwd2'])
      // 验证是否允许提交
      refer(){
        for(let i in this.register){
          if(!this.register[i].testingc || this.register[i].value.trim() == ''){
            return false;
          }
        }
        return true;
      }
    },

    //方法
    methods: {
      // ...mapMutations(['checkPassword'])

      // 切换查看密码
      checkPassword(){
        this.register.pwd.see = !this.register.pwd.see;
      },

      // 验证用户账号
      testingcAcount(){
        let object = this.register.account;
        object.testingc = object.pat.test(object.value);
        this.IsUserimg = true;
      },

      // 验证用户密码
      testingcPwd(){
        let object = this.register.pwd;
        object.testingc = object.pat.test(object.value);
      },

      // 登陆按钮
      landLoading(){
        this.island = true;
        let User = this.register;
        let isButImg = getUserName(User.account.value);
        if(isButImg){

          if(isButImg.account == User.account.value && isButImg.pwd == User.pwd.value){
            this.$notify({
              type: 'success',
              message: '登陆成功',
              duration: 2000,
              onClose: () => {
                // 存储cookie
                console.log(isButImg)
                this.island = false;
                tianjia('LandUser', User.account.value, 20);
                this.$router.push({name: 'index'});
              }
            })
          }else{
            this.$notify({
              type: 'danger',
              message: '账号或者密码错误',
              duration: 3000,
              onClose: () => {
                this.island = false;
              }
            })
          }

        }else{
          this.$notify({
            type: 'warning',
            message: '没有找到该用户名，请检查后再登陆！',
            duration: 3000,
            onClose: () => {
              this.island = false;
            }
          });
        }
      },

      //显示用户图片
      showUserimg(){
        if(this.IsUserimg){
          this.IsUserimg = false;
          let UserNameId = this.register.account.value;
          let isButImg = getUserName(UserNameId);
          if(isButImg){
            this.NameSrc = isButImg.url.content;
          }else{
            this.NameSrc = null;
          }
        }
      }


    },
		// 钩子
		created(){
      // console.log(this)
      // 命名空间用法
      // this.$store.commit('startdata/Initialization');
      // console.log(this.$store.getters.pop)
      // this.$store.getters.Initialization;
		}

  }
</script>

<style lang="less" scoped>
  .trimming{
    margin: .5rem 0;
  }
  .register{
    .app-logo{
      height: 5.333rem;
      position: relative;
      background: url(../assets/image/Starry.jpg) no-repeat center center;
      background-size: cover;
      .logo{
        position: absolute;
        top: 0;
        bottom: 0;
        left: 0;
        right: 0;
        margin: auto;
        // transform: translate(-50%, -50%);
        background-color: #ddd;
        width: 2.133rem;
        height: 2.133rem;
        border-radius: 50%;
      }
    }

    .register-form{
      padding: 0.266rem;
    }
    .register-btn{
      margin-top: 0.533rem;
    }
    .login{
      margin-top: 0.533rem;
      text-align: center;
    }
  }
</style>
